import io from "socket.io-client";
import React, {useEffect, useState} from "react";
import "../css/Chat.css"
import $ from "jquery";
import {Simulate} from "react-dom/test-utils";
import ended = Simulate.ended;

export default function ChatRoom() {

    const [message, setMessage] = useState('');
    const [userId, setUserId] = useState('');

    const socket: any = io('http://localhost:5000')

    const handleRequestSocket = () => {

        const data = {
            message,
            userId
        }

        socket.emit("send", data)
    }



    useEffect(() => {

        socket.on("receive", (socket: any) => {
            // @ts-ignore
            var nUserId = document.getElementById("nickName").value;

            const li = document.createElement("li");
            li.classList.add((nUserId == socket.userId ? "sent" : "received"));
            li.innerHTML = ` <span class="userProfile">
                                <span class="userId">${socket.userId}</span>
                                <img src="https://placeimg.com/50/50/any" alt={"any"}/>
                            </span>
                        <span class="message">${socket.message}</span>
                        <span class="time">${socket.time}</span>`;

            $(function () {
                var chatList = $(".chatting_list")
                var chatInput = $(".chatInput")

                chatList.append(li);
                chatInput.val("");

                $("#ChatTextBox").scrollTop($("#ChatTextBox")[0].scrollHeight);
            })
        })

    }, [])

    return (
        <div className={"ChatBox shadow-lg"}>

            <div className={"ChatUserInfo"}>
                <label form={"nickName"}></label>
                <input id={"nickName"} value={userId} onChange={(e) => setUserId(e.target.value)}/>
            </div>

            <div className={"ChatTextBox"} id={"ChatTextBox"}>
                <ul className={"chatting_list"}>

                </ul>
            </div>

            <div className={"ChatInputBox"}>
                    <span>
                        <input type="text" className={"chatInput"} value={message}
                               onChange={(e) => setMessage((e.target.value))}/>
                        <button onClick={handleRequestSocket}> Request</button>
                    </span>
            </div>

        </div>
    )
}